'use client';

import { useState, useEffect } from 'react';
import { MissionState } from '../../types/dashboard';

interface Props {
  data: MissionState;
}

const statusIcon = (status: MissionState['status']) => {
  const icons = { Nominal: 'üü¢', Degraded: 'üü°', Critical: 'üî¥' };
  return icons[status];
};

export const DashboardHeader: React.FC<Props> = ({ data }) => {
  const [time, setTime] = useState(new Date().toLocaleTimeString('en-IN', {
    hour: '2-digit',
    minute: '2-digit',
    timeZone: 'Asia/Kolkata',
  }));

  useEffect(() => {
    const iv = setInterval(
      () =>
        setTime(
          new Date().toLocaleTimeString('en-IN', {
            hour: '2-digit',
            minute: '2-digit',
          })
        ),
      1000 * 30
    );
    return () => clearInterval(iv);
  }, []);

  return (
    <header className="h-[80px] bg-black/80 backdrop-blur-md border-b border-cyan-500/30 flex items-center px-6 fixed w-full z-50">
      <div className="flex items-center space-x-4">
        <div className="w-12 h-12 bg-gradient-to-r from-cyan-500 to-blue-500 rounded-lg glow-cyan animate-pulse"></div>
        <div>
          <h1 className="text-2xl font-bold font-mono text-white tracking-tight">
            {data.name}
          </h1>
          <div className="flex items-center space-x-3 text-sm opacity-90">
            <span className="px-3 py-1 bg-gray-900 rounded-full text-cyan-400">
              {data.phase}
            </span>
            <span className="text-xl">{statusIcon(data.status)}</span>
            <span>{time}</span>
            {data.anomalyCount > 0 && (
              <span className="bg-red-500/20 text-red-400 px-2 py-1 rounded-full text-xs font-mono animate-pulse">
                {data.anomalyCount} Alerts
              </span>
            )}
          </div>
        </div>
      </div>
    </header>
  );
};
