'use client';

import { MissionPhase } from '../../types/mission';

interface Props {
  phases: MissionPhase[];
}

export const PhaseTimeline: React.FC<Props> = ({ phases }) => {
  const activeIndex = phases.findIndex((p) => p.isActive);
  const activePhase = phases[activeIndex];

  return (
    <div className="space-y-4">
      <h3 className="text-lg font-semibold text-gray-800 mb-4">Mission Phase</h3>
      <div className="relative">
        <div className="flex items-center w-full h-3 bg-gray-200 rounded overflow-hidden">
          {phases.map((phase) => (
            <div
              key={phase.name}
              className={`h-3 transition-all ${
                phases.indexOf(phase) <= activeIndex ? 'bg-blue-500' : 'bg-gray-300'
              }`}
              style={{ width: `${100 / phases.length}%` }}
            />
          ))}
        </div>

        {activePhase && (
          <div className="mt-4 p-3 bg-blue-50 border border-blue-200 rounded">
            <div className="text-sm text-gray-600 mb-1">{activePhase.name}</div>
            <div className="text-lg font-semibold text-blue-600">
              {activePhase.progress}% ‚Ä¢ {activePhase.eta}
            </div>
          </div>
        )}
      </div>

      <div className="grid grid-cols-5 gap-2 mt-6">
        {phases.map((phase) => (
          <div
            key={phase.name}
            className={`p-2 rounded text-center text-xs border transition-all ${
              phase.isActive
                ? 'bg-blue-100 border-blue-500 font-semibold text-blue-700'
                : 'bg-white border-gray-300 text-gray-600'
            }`}
          >
            <div className="truncate text-xs">{phase.name.split(' ')[0]}</div>
            <div className="font-bold mt-1 text-sm">{phase.progress}%</div>
          </div>
        ))}
      </div>
    </div>
  );
};
