'use client';

import { Satellite } from '../../types/mission';

interface Props extends Satellite {
  onClick?: () => void;
  isSelected?: boolean;
}

export const SatelliteCard: React.FC<Props> = ({
  orbitSlot,
  status,
  latency,
  task,
  signal,
  onClick,
  isSelected,
}) => {
  const statusColor = status === 'Nominal' ? 'text-green-600' : status === 'Degraded' ? 'text-yellow-600' : 'text-red-600';
  const borderColor = status === 'Nominal' ? 'border-green-300' : status === 'Degraded' ? 'border-yellow-300' : 'border-red-300';

  return (
    <div
      className={`p-4 border rounded-lg cursor-pointer hover:shadow-md transition-shadow ${borderColor} ${isSelected ? 'ring-2 ring-blue-500 bg-blue-50' : 'bg-white'}`}
      onClick={onClick}
    >
      <div className="flex items-center justify-between mb-2">
        <span className="text-sm font-semibold text-gray-700">LEO-{orbitSlot}</span>
        <span className={`text-lg font-bold ${statusColor}`}>‚óè</span>
      </div>
      <div className="text-sm font-semibold text-gray-800 mb-2">{status}</div>
      <div className="text-xs text-gray-600 space-y-1 mb-3">
        <div>{Math.round(latency)}ms | {task}</div>
        <div>Signal: {signal}%</div>
      </div>
      <div className="w-full bg-gray-200 rounded h-2 overflow-hidden">
        <div
          className="bg-blue-500 h-2 transition-all"
          style={{ width: `${Math.min(signal, 100)}%` }}
        />
      </div>
    </div>
  );
};
